/* eslint-disable */
import React, { Component } from 'react'
import Table from '@/components/table'//路径别名
import ReposList from '@/components/reposlist' 

class popular extends Component {
    constructor(props){
        super(props)
        this.state={
            curUrl:''
        }
    }

    handelChangeRequestUrl(args){
        sessionStorage.setItem('curUrl',args)
        this.setState({
            curUrl:args
        })
    }

    render() {
        const tableNavUrl = this.props.match
        return (
          <div>
            {/* 获取数据的组件 */}
            <Table tableNavUrl={tableNavUrl} onChangeRequestUrl={this.handelChangeRequestUrl.bind(this)} />
            {/* 渲染数据的组件 */}
            <ReposList curUrl={this.state.curUrl} />
          </div>
        )
    }
}

export default popular
