/* eslint-disable */
    import React, { Component } from 'react'

    import './table.less'

    import { HashRouter , NavLink } from 'react-router-dom'
    import { Row, Col } from 'antd';

    class table extends Component {
        constructor(props){
            super(props)
        }

        ChangeRequestUrl(curUrl){
            this.props.onChangeRequestUrl(curUrl)
        }

        render() {      
            const { tableNavUrl } = this.props
            const { url } = tableNavUrl
            return (
              <div>
                <HashRouter>
                  <Row>
                    <Col xs={{span:20 , offset:2}} lg={{span:12 , offset:6}}>
                      <ul className="table-ul">
                        <li>
                          <NavLink 
                            to={`${url}/all`} 
                            activeClassName="handle" 
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            All
                          </NavLink>
                        </li>
                        <li>
                          <NavLink 
                            to={`${url}/javascript`} 
                            activeClassName="handle"
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1+language:javascript&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            JavaScript
                          </NavLink>
                        </li>
                        <li>
                          <NavLink 
                            to={`${url}/ruby`} 
                            activeClassName="handle"
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1+language:ruby&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            Ruby
                          </NavLink>
                        </li>
                        <li>
                          <NavLink 
                            to={`${url}/java`} 
                            activeClassName="handle"
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1+language:java&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            Java
                          </NavLink>
                        </li>
                        <li>
                          <NavLink 
                            to={`${url}/css`} 
                            activeClassName="handle"
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1+language:css&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            CSS
                          </NavLink>
                        </li>
                        <li>
                          <NavLink 
                            to={`${url}/python`} 
                            activeClassName="handle"
                            onClick={this.ChangeRequestUrl.bind(this,'https://api.github.com/search/repositories?q=stars:%3E1+language:python&sort=stars&order=desc&type=Repositories&page=')}
                          >
                            Python
                          </NavLink>
                        </li>
                      </ul>
                    </Col>
                  </Row>
                </HashRouter>
              </div>
            )
        }
    }
    export default table
