import React, { Component } from 'react'
import './reposlist.less';

import axios from 'axios'
import InfiniteScroll from 'react-infinite-scroller';

import { Spin, Row, Col, message, Alert } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar, faUser, faExclamationTriangle, faCodeBranch } from '@fortawesome/free-solid-svg-icons'

export default class reposlist extends Component {
  constructor(props) {
    super(props)
    this.state = {
      repos: [],
      isloading: true,
      isloadingMore: false,
      hasMore: true,
      initPage: 1,
      curPage: 1,
      url: '',
      prevUrl: ''
    }
  }

  // static getDerivedStateFromProps(nextProps, prevState) {
  //   const { curUrl } = nextProps
  //   if (curUrl !== prevState.url) {
  //     return {
  //       isloading: true,
  //       url: curUrl
  //     }
  //   }
  //   return null
  // }

  async componentDidMount() {
    const res = await axios({
      url: !!sessionStorage.getItem('curUrl') ? sessionStorage.getItem('curUrl').concat(this.state.curPage) : 'https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories&page=1',
      method: 'GET'
    }).catch((err) => {
      console.log(err);
    })
    this.setState({
      url: !!sessionStorage.getItem('curUrl') ? sessionStorage.getItem('curUrl') : 'https://api.github.com/search/repositories?q=stars:%3E1&sort=stars&order=desc&type=Repositories&page=',
      repos: res.data.items,
      isloading: false,
    })
  }

  render() {
    const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
    const colStyle = { textAlign: 'center', display: 'block' }
    return (
      <div className="list-box">
        <Row>
          {
            this.state.isloading ? (
              <Col
                xs={{ span: 8, offset: 8 }}
                lg={{ span: 20, offset: 2 }}
                style={colStyle}
              >
                <Spin tip="Loading..." indicator={antIcon} />
              </Col>
            )
              :
              (
                <Col
                  xs={{ span: 24 }}
                  lg={{ span: 22, offset: 1 }}
                  style={colStyle}
                >
                  <ul className="list-ul" ref={(ref) => this.scrollParentRef = ref}>
                    <InfiniteScroll
                      className="InfiniteScroll"
                      pageStart={0}
                      loadMore={this.loadFunc.bind(this)}
                      hasMore={true || false}
                      loader={<Spin tip="Loading More..." indicator={antIcon} style={{ display: 'block', width: '100%' }} />}
                    >
                      {
                        this.state.repos.map((it, index) => {
                          return (
                            <li key={index}>
                              <figure>
                                <h3>#{index + 1}</h3>
                                <img src={it.owner.avatar_url} />
                                <figcaption>{it.owner.login}</figcaption>
                                <figcaption>
                                  <ul className="icon-ul">
                                    <li><FontAwesomeIcon icon={faUser} color="#fdc265" /></li>
                                    <li><FontAwesomeIcon icon={faStar} color="#ffd802" /></li>
                                    <li><FontAwesomeIcon icon={faCodeBranch} color="#7cc5fa" /></li>
                                    <li><FontAwesomeIcon icon={faExclamationTriangle} color="#f28b8f" /></li>
                                  </ul>
                                  <ul className="msg-ul">
                                    <li><span><a href={it.clone_url}>{it.owner.login}</a></span></li>
                                    <li><span>{it.stargazers_count} stars</span></li>
                                    <li><span>{it.forks_count} forks</span></li>
                                    <li><span>{it.open_issues_count} open issues</span></li>
                                  </ul>
                                </figcaption>
                              </figure>
                            </li>
                          )
                        })
                      }
                    </InfiniteScroll>
                  </ul>
                </Col>
              )
          }
        </Row>
      </div >
    )
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    console.log(nextProps, prevState);
    if (nextProps.curUrl !== prevState.prevUrl) {
      return {
        isloading: true,
        prevUrl: nextProps.curUrl
      }
    }
    return null
  }

  async shouldComponentUpdate(nextProps, nextState) {
    console.log('shouldComponentUpdate');
    console.log(nextProps, nextState);
    if (nextState.url !== nextProps.curUrl) {
      const { curUrl } = nextProps
      // sessionStorage.setItem('curUrl', curUrl)
      const res = await axios.get(curUrl.concat(this.state.initPage))
        .catch((err) => {
          console.log(err);
        })
      this.setState({
        repos: res.data.items,
        isloading: false,
        url: curUrl
      })
      return true
    }
    return false
  }

  async loadFunc() {
    console.log("加载更多");
    const addpage = this.state.curPage + 1
    const res = await axios.get(this.state.url.concat(addpage))
    const addRepos = this.state.repos.concat(res.data.items)
    this.setState({
      repos: addRepos,
      curPage: addpage
    })
  }

}
/*
    头像：avatar_url: "https://avatars.githubusercontent.com/u/48942249?v=4"
    名字：owner.login: "freeCodeCamp"
    星星：stargazers_count: 328242
    引用：forks_count: 26294
    问题：open_issues_count: 227
*/